// colors
[data-dark="false"] {
  --primary: #6883A6;
  --secondary: #F2B56B;
  --text: #2a2a2a;
  --background: #ffffff;
  --background-alt: #fafafa;
  --light-gray: #e0e0e0;
  --gray: #808080;
  --overlay: #00000000;
}
[data-dark="true"] {
  --primary: #343440;
  --secondary: #6883A6;
  --text: #ffffff;
  --background: #181818;
  --background-alt: #1c1c1c;
  --light-gray: #404040;
  --gray: #808080;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: "Lato", sans-serif;
  --heading: "Lato", sans-serif;
  --body: "Lato", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --medium: 1.1rem;
  --large: 1.3rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 1.7;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
}
